<template>
    <div class="container">
        <div class="main__posts posts" v-if="posts">
            <div class="main__posts-item" v-for="post in posts" :key="post.id">
                <p>Name: {{ post.name }}</p>
                <p>Email: {{ post.email }}</p>
                <p>City: {{ post.body }}</p>
            </div>
        </div>
        <div v-else class="Loading">
            Идет загрузка...
        </div>
    </div>
</template>

<script>
    export default {
        
        created() {
            this.getPosts()
        },
        computed: {
            posts() {
            return this.$store.state.posts
            }
        },
        methods:{
            getPosts() {
            this.$store.dispatch('getPosts')
            }
        }
        
    }
    
</script>
